'use client';

import React from 'react';

import {
  useGetAuthUserQuery,
  useUpdateTenantSettingsMutation,
} from '@/state/api';
import { SettingsFormData } from '@/lib/schemas';

const TenantSettings = () => {
  const { data: authUser, isLoading: authLoading } = useGetAuthUserQuery();
  console.log('authUser::', authUser);

  const [updateTenantSettings, { isLoading: updateLoading }] =
    useUpdateTenantSettingsMutation();

  if (authLoading || updateLoading) return <div>Loading...</div>;

  const { cognitoId, name, email, phoneNumber } = authUser?.userInfo ?? {};

  const initialData = {
    name,
    email,
    phoneNumber,
  };

  const handleSubmit = async (data: typeof initialData) => {
    if (!cognitoId) return;

    await updateTenantSettings({
      cognitoId,
      ...data,
    });
  };

  return <div>TenantSettings</div>;
};

export default TenantSettings;
